<template>
  <div>
    <v-list-tile :disabled="disabled" @click.prevent="vModel = !vModel" ripple>
      <v-list-tile-action v-if="showAction">
        <slot name="action">
          <v-checkbox v-model="vModel" v-if="type === 'checkbox'"></v-checkbox>
          <v-switch v-model="vModel"></v-switch>
        </slot>
      </v-list-tile-action>
      <v-list-tile-content>
        <v-list-tile-title>{{title}}</v-list-tile-title>
        <v-list-tile-sub-title>{{description}}</v-list-tile-sub-title>
      </v-list-tile-content>
    </v-list-tile>
    <slot/>
  </div>
</template>

<script>
import { vModel } from "../../mixins/vModel.js";

export default {
  mixins: [vModel],
  props: {
    title: {
      type: String,
      default: "Unnamed setting item"
    },
    description: {
      type: String,
      default: "No setting description"
    },
    type: {
      type: String,
      default: "other",
      validator: val => ["checkbox", "switch", "other"].indexOf(val) !== -1
    },
    showAction: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {};
  },
  methods: {}
};
</script>
