<template>
  <settings-section title="GUI Settings">
    <settings-item type="other" v-model="themeDialog" title="Theme">
      <settings-dialog title="Theme" v-model="themeDialog">
        <theme-item
          v-for="(t, idx) in availableThemes"
          :key="idx"
          :primary-color="t.primary"
          :secondary-color="t.secondary"
          :accent-color="t.accent"
          class="my-3"
          @click.native="setTheme(t)"
        />
      </settings-dialog>
    </settings-item>
    <settings-item
      :value="theme.dark"
      @input="setDark"
      type="switch"
      :title="$t('theme.dark')"
      show-action
    ></settings-item>
    <settings-item disabled type="checkbox" v-model="language" title="Language"></settings-item>
  </settings-section>
</template>

<script>
import SettingsSection from "./SettingsSection";
import SettingsItem from "./SettingsItem";
import SettingsDialog from "./SettingsDialog";
import ThemeItem from "./ThemeItem";
import { mapMutations, mapGetters } from "vuex";
import colors from "vuetify/es5/util/colors";

export default {
  components: {
    SettingsSection,
    SettingsItem,
    ThemeItem,
    SettingsDialog
  },
  data() {
    return {
      themeDialog: false,
      language: "asdf",
      availableThemes: [
        {
          primary: "#456CB2",
          accent: colors.green.base,
          secondary: colors.blueGrey.base
        },
        {
          primary: colors.deepOrange.base,
          accent: colors.orange.darken1,
          secondary: colors.blueGrey.base
        },
        {
          primary: colors.pink.base,
          accent: colors.lightBlue.base,
          secondary: colors.blueGrey.base
        }
      ]
    };
  },
  computed: {
    ...mapGetters({
      theme: "settings/theme"
    })
  },
  methods: {
    ...mapMutations({
      setTheme: "settings/setTheme",
      setDark: "settings/setDark"
    })
  }
};
</script>
