<template>
    <v-dialog v-model="vModel" :max-width="isTextbox ? '600px' : '300px'" :scrollable="isSelect">
        <v-card>
            <v-card-title>
                <span class="headline">{{title}}</span>
            </v-card-title>
            <v-card-text :style="{height: isSelect ? '300px' : undefined }">
                <slot></slot>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" flat @click.native="vModel = false">Close</v-btn>
                <v-btn color="primary" flat @click.native="vModel = false">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import { vModel } from "../../mixins/vModel.js";

export default {
  mixins: [vModel],
  props: {
    form: {
      type: String,
      validator: v => ["textbox", "select"].indexOf(v) !== -1
    },
    title: {
      type: String,
      default: ""
    }
  },
  computed: {
    isTextbox() {
      return this.form === "textbox";
    },
    isSelect() {
      return this.form === "select";
    }
  }
};
</script>
